.ol-box {
  box-sizing: border-box;
  border-radius: 2px;
  border: 2px solid blue;
}

.ol-mouse-position {
  top: 8px;
  right: 8px;
  position: absolute;
}

.ol-scale-line {
  background: rgba(0,60,136,0.3);
  border-radius: 4px;
  bottom: 8px;
  left: 8px;
  padding: 2px;
  position: absolute;
}
.ol-scale-line-inner {
  border: 1px solid #eee;
  border-top: none;
  color: #eee;
  font-size: 10px;
  text-align: center;
  margin: 1px;
  will-change: contents, width;
}
.ol-overlay-container {
  will-change: left,right,top,bottom;
}

.ol-unsupported {
  display: none;
}
.ol-viewport, .ol-unselectable {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -webkit-tap-highlight-color: rgba(0,0,0,0);
}
.ol-selectable {
  -webkit-touch-callout: default;
  -webkit-user-select: text;
  -moz-user-select: text;
  -ms-user-select: text;
  user-select: text;
}
.ol-grabbing {
  cursor: -webkit-grabbing;
  cursor: grabbing;
}
.ol-grab {
  cursor: move;
  cursor: -webkit-grab;
  cursor: grab;
}
.ol-control {
  position: absolute;
  background-color: rgba(255,255,255,0.4);
  border-radius: 4px;
  padding: 2px;
}
.ol-control:hover {
  background-color: rgba(255,255,255,0.6);
}
.ol-zoom {
  top: .5em;
  left: .5em;
}
.ol-rotate {
  top: .5em;
  right: .5em;
  transition: opacity .25s linear, visibility 0s linear;
}
.ol-rotate.ol-hidden {
  opacity: 0;
  visibility: hidden;
  transition: opacity .25s linear, visibility 0s linear .25s;
}
.ol-zoom-extent {
  top: 4.643em;
  left: .5em;
}
.ol-full-screen {
  right: .5em;
  top: .5em;
}
@media print {
  .ol-control {
    display: none;
  }
}

.ol-control button {
  display: block;
  margin: 1px;
  padding: 0;
  color: white;
  font-size: 1.14em;
  font-weight: bold;
  text-decoration: none;
  text-align: center;
  height: 1.375em;
  width: 1.375em;
  line-height: .4em;
  background-color: rgba(0,60,136,0.5);
  border: none;
  border-radius: 2px;
}
.ol-control button::-moz-focus-inner {
  border: none;
  padding: 0;
}
.ol-zoom-extent button {
  line-height: 1.4em;
}
.ol-compass {
  display: block;
  font-weight: normal;
  font-size: 1.2em;
  will-change: transform;
}
.ol-touch .ol-control button {
  font-size: 1.5em;
}
.ol-touch .ol-zoom-extent {
  top: 5.5em;
}
.ol-control button:hover,
.ol-control button:focus {
  text-decoration: none;
  background-color: rgba(0,60,136,0.7);
}
.ol-zoom .ol-zoom-in {
  border-radius: 2px 2px 0 0;
}
.ol-zoom .ol-zoom-out {
  border-radius: 0 0 2px 2px;
}


.ol-attribution {
  text-align: right;
  bottom: .5em;
  right: .5em;
  max-width: calc(100% - 1.3em);
}

.ol-attribution ul {
  margin: 0;
  padding: 0 .5em;
  font-size: .7rem;
  line-height: 1.375em;
  color: #000;
  text-shadow: 0 0 2px #fff;
}
.ol-attribution li {
  display: inline;
  list-style: none;
  line-height: inherit;
}
.ol-attribution li:not(:last-child):after {
  content: " ";
}
.ol-attribution img {
  max-height: 2em;
  max-width: inherit;
  vertical-align: middle;
}
.ol-attribution ul, .ol-attribution button {
  display: inline-block;
}
.ol-attribution.ol-collapsed ul {
  display: none;
}
.ol-attribution:not(.ol-collapsed) {
  background: rgba(255,255,255,0.8);
}
.ol-attribution.ol-uncollapsible {
  bottom: 0;
  right: 0;
  border-radius: 4px 0 0;
  height: 1.1em;
  line-height: 1em;
}
.ol-attribution.ol-uncollapsible img {
  margin-top: -.2em;
  max-height: 1.6em;
}
.ol-attribution.ol-uncollapsible button {
  display: none;
}

.ol-zoomslider {
  top: 4.5em;
  left: .5em;
  height: 200px;
}
.ol-zoomslider button {
  position: relative;
  height: 10px;
}

.ol-touch .ol-zoomslider {
  top: 5.5em;
}

.ol-overviewmap {
  left: 0.5em;
  bottom: 0.5em;
}
.ol-overviewmap.ol-uncollapsible {
  bottom: 0;
  left: 0;
  border-radius: 0 4px 0 0;
}
.ol-overviewmap .ol-overviewmap-map,
.ol-overviewmap button {
  display: inline-block;
}
.ol-overviewmap .ol-overviewmap-map {
  border: 1px solid #7b98bc;
  height: 150px;
  margin: 2px;
  width: 150px;
}
.ol-overviewmap:not(.ol-collapsed) button{
  bottom: 1px;
  left: 2px;
  position: absolute;
}
.ol-overviewmap.ol-collapsed .ol-overviewmap-map,
.ol-overviewmap.ol-uncollapsible button {
  display: none;
}
.ol-overviewmap:not(.ol-collapsed) {
  background: rgba(255,255,255,0.8);
}
.ol-overviewmap-box {
  border: 2px dotted rgba(0,60,136,0.7);
}

.ol-overviewmap .ol-overviewmap-box:hover {
  cursor: move;
}

.rtf {
  box-sizing: border-box;
  margin: 25px;
  position: fixed;
  white-space: nowrap;
  z-index: 30;
  padding-left: 0;
  list-style: none;
}

.rtf.open .rtf--mb > * {
  -webkit-transform-origin: center center;
          transform-origin: center center;
  -webkit-transform: rotate(315deg);
          transform: rotate(315deg);
  transition: ease-in-out transform .2s;
}

.rtf.open .rtf--mb > ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.rtf.open .rtf--ab__c:hover > span {
  transition: ease-in-out opacity .2s;
  opacity: 0.9;
}

.rtf.open .rtf--ab__c:nth-child(1) {
  -webkit-transform: translateY(-60px) scale(1);
          transform: translateY(-60px) scale(1);
  transition-delay: 0.03s;
}

.rtf.open .rtf--ab__c:nth-child(1).top {
  -webkit-transform: translateY(60px) scale(1);
          transform: translateY(60px) scale(1);
}

.rtf.open .rtf--ab__c:nth-child(2) {
  -webkit-transform: translateY(-120px) scale(1);
          transform: translateY(-120px) scale(1);
  transition-delay: 0.09s;
}

.rtf.open .rtf--ab__c:nth-child(2).top {
  -webkit-transform: translateY(120px) scale(1);
          transform: translateY(120px) scale(1);
}

.rtf.open .rtf--ab__c:nth-child(3) {
  -webkit-transform: translateY(-180px) scale(1);
          transform: translateY(-180px) scale(1);
  transition-delay: 0.12s;
}

.rtf.open .rtf--ab__c:nth-child(3).top {
  -webkit-transform: translateY(180px) scale(1);
          transform: translateY(180px) scale(1);
}

.rtf.open .rtf--ab__c:nth-child(4) {
  -webkit-transform: translateY(-240px) scale(1);
          transform: translateY(-240px) scale(1);
  transition-delay: 0.15s;
}

.rtf.open .rtf--ab__c:nth-child(4).top {
  -webkit-transform: translateY(240px) scale(1);
          transform: translateY(240px) scale(1);
}

.rtf.open .rtf--ab__c:nth-child(5) {
  -webkit-transform: translateY(-300px) scale(1);
          transform: translateY(-300px) scale(1);
  transition-delay: 0.18s;
}

.rtf.open .rtf--ab__c:nth-child(5).top {
  -webkit-transform: translateY(300px) scale(1);
          transform: translateY(300px) scale(1);
}

.rtf.open .rtf--ab__c:nth-child(6) {
  -webkit-transform: translateY(-360px) scale(1);
          transform: translateY(-360px) scale(1);
  transition-delay: 0.21s;
}

.rtf.open .rtf--ab__c:nth-child(6).top {
  -webkit-transform: translateY(360px) scale(1);
          transform: translateY(360px) scale(1);
}

.rtf--mb__c {
  padding: 25px;
  margin: -25px;
}

.rtf--mb__c *:last-child {
  margin-bottom: 0;
}

.rtf--mb {
  height: 56px;
  width: 56px;
  z-index: 20;
  background-color: #666666;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  position: relative;
  border: none;
  border-radius: 50%;
  box-shadow: 0 0 4px rgba(0, 0, 0, 0.14), 0 4px 8px rgba(0, 0, 0, 0.28);
  cursor: pointer;
  outline: none;
  padding: 0;
  -webkit-user-drag: none;
  font-weight: bold;
  color: #f1f1f1;
  text-decoration: none;
  font-size: 18px;
}

.rtf--mb > * {
  transition: ease-in-out transform .2s;
}

.rtf--ab__c {
  display: block;
  position: absolute;
  top: 0;
  right: 1px;
  padding: 10px 0;
  margin: -10px 0;
  transition: ease-in-out transform .2s;
}

.rtf--ab__c > span {
  opacity: 0;
  transition: ease-in-out opacity 0.2s;
  position: absolute;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  margin-right: 6px;
  background: rgba(0, 0, 0, 0.75);
  padding: 2px 4px;
  border-radius: 2px;
  color: white;
  font-size: 13px;
  box-shadow: 0 0 4px rgba(0, 0, 0, 0.14), 0 4px 8px rgba(0, 0, 0, 0.28);
}

.rtf--ab__c > span.right {
  right: 100%;
}

.rtf--ab__c:nth-child(1) {
  -webkit-transform: translateY(-60px) scale(0);
          transform: translateY(-60px) scale(0);
  transition-delay: 0.21s;
}

.rtf--ab__c:nth-child(1).top {
  -webkit-transform: translateY(60px) scale(0);
          transform: translateY(60px) scale(0);
}

.rtf--ab__c:nth-child(2) {
  -webkit-transform: translateY(-120px) scale(0);
          transform: translateY(-120px) scale(0);
  transition-delay: 0.18s;
}

.rtf--ab__c:nth-child(2).top {
  -webkit-transform: translateY(120px) scale(0);
          transform: translateY(120px) scale(0);
}

.rtf--ab__c:nth-child(3) {
  -webkit-transform: translateY(-180px) scale(0);
          transform: translateY(-180px) scale(0);
  transition-delay: 0.15s;
}

.rtf--ab__c:nth-child(3).top {
  -webkit-transform: translateY(180px) scale(0);
          transform: translateY(180px) scale(0);
}

.rtf--ab__c:nth-child(4) {
  -webkit-transform: translateY(-240px) scale(0);
          transform: translateY(-240px) scale(0);
  transition-delay: 0.12s;
}

.rtf--ab__c:nth-child(4).top {
  -webkit-transform: translateY(240px) scale(0);
          transform: translateY(240px) scale(0);
}

.rtf--ab__c:nth-child(5) {
  -webkit-transform: translateY(-300px) scale(0);
          transform: translateY(-300px) scale(0);
  transition-delay: 0.09s;
}

.rtf--ab__c:nth-child(5).top {
  -webkit-transform: translateY(300px) scale(0);
          transform: translateY(300px) scale(0);
}

.rtf--ab__c:nth-child(6) {
  -webkit-transform: translateY(-360px) scale(0);
          transform: translateY(-360px) scale(0);
  transition-delay: 0.03s;
}

.rtf--ab__c:nth-child(6).top {
  -webkit-transform: translateY(360px) scale(0);
          transform: translateY(360px) scale(0);
}

.rtf--ab {
  height: 48px;
  width: 48px;
  background-color: #AAAAAA;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  position: relative;
  border: none;
  border-radius: 50%;
  box-shadow: 0 0 4px rgba(0, 0, 0, 0.14), 0 4px 8px rgba(0, 0, 0, 0.28);
  cursor: pointer;
  outline: none;
  padding: 0;
  -webkit-user-drag: none;
  font-weight: bold;
  color: #f1f1f1;
  margin-right: 4px;
  font-size: 16px;
}

